@import 'https://fonts.googleapis.com/css?family=Share+Tech+Mono';
@font-face {
  font-family: 'Digital';
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot');
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix')
      format('embedded-opentype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2')
      format('woff2'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff')
      format('woff'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf')
      format('truetype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7')
      format('svg');
}

body {
  background-color: #c2c2d6;
  box-sizing: border-box;
}

#wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#calculator {
  border: 1px solid black;
  width: auto;
  height: auto;
  background-color: black;
  padding: 5px;
}

#display {
  background-color: black;
  font-family: digital;
  text-align: right;
  height: auto;
  color: orange;
  margin: 0px;
  font-size: 45px;
}

.buttons {
  display: grid;
  grid-template-columns: 80px 80px 80px 80px;
  grid-template-rows: 70px 70px 70px 70px 70px;
  grid-template-areas: 
    "power power division product"
    "seven eight nine minus"
    "four five six plus"
    "one two three equals"
    "zero zero period equals";
    gap: 1px 1px;
}

.button {
  font-family: Share Tech Mono, monospace;
  font-size: 20px;
  background-color: #4d4d4d;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

#clear {
  grid-area: power;
  background-color: #b33939;
}

#divide {
  grid-area: division;
  background-color: #6d6d6d;
}

#multiply {
  grid-area: product;
  background-color: #6d6d6d;
}

#subtract {
  grid-area: minus;
  background-color: #6d6d6d;
}

#add {
  grid-area: plus;
  background-color: #7d7d7d;
}

#equals {
  grid-area: equals;
  background-color: #0a3d62;
}

#decimal {
  grid-area: period;
}

#zero {
  grid-area: zero;
}

#one {
  grid-area: one;
}

#two {
  grid-area: two;
}

#three {
  grid-area: three;
}

#four {
  grid-area: four;
}

#five {
  grid-area: five;
}

#six {
  grid-area: six;
}

#seven {
  grid-area: seven;
}

#eight {
  grid-area: eight;
}

#nine {
  grid-area: nine;
}